import { Typography, Button as AButton, Form } from "antd";
import { useDispatch, useSelector } from "react-redux";
import React, { useEffect, useMemo } from "react";
import { checkLogin } from "../../../redux/auth/AuthActions";
import { Link, useNavigate } from "react-router-dom";
import { AUTH_ROUTES } from "../../../contants/RoutesEnum";
import { setAppLoader } from "../../../redux/common/CommonActions";
import IconFormInput from "../../components/Inputs/IconFormInput";
import { ReactComponent as MailIcon } from "../../../assets/icons/mail.svg";
import { ReactComponent as LockIcon } from "../../../assets/icons/lock.svg";
import SvgConverter from "../../components/Common/SvgConverter";
import { uniqueID } from "../../../utilz/utilz";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';

const LoginForm = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.auth);
  const { appLoader } = useSelector((state) => state.common);
  const [form] = Form.useForm();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const onSubmit = (data) => {
    dispatch(checkLogin({ ...data, applicationID: 2, callback: handleLogin }));
  };
  useEffect(() => {
    dispatch(setAppLoader(false));
  }, [appLoader]);

  const handleLogin = (data) => {
    if (data?.twoFactorEnabled === true) {
      navigate(AUTH_ROUTES.USER_SEND_AUTHENTICATION_TOKEN);
    }
  };

  return (
    <Form
      id={uniqueID()}
      className="mt-30"
      onFinish={onSubmit}
      initialValues={
        {
          // username: "maitree.rc@gmail.com",
          // password: "jnzrrh",
          // username: "carrier.test@tranistics.com",
          // password: "pass123",
          // username: "dassaishree1993@gmail.com",
          // password: "pass123",
          // username: "asalinas@botslogistics.com",
          // password: "Astridnvision@123456",
        }
      }
      form={form}
    >
      <Typography.Text id={uniqueID()} type="danger" className="f-13">
        {user?.error ? user.message : ""}
      </Typography.Text>
      <IconFormInput
        editMode={true}
        className="mb-30"
        name={"username"}
        rules={[
          { required: true, message: t('translation:default.notification.email') },
          { type: "email", message: t("translation:login.form.email.enter") },
        ]}
        placeholder={t("translation:login.form.email.placeholder")}
        icon={
          <SvgConverter
            Icon={MailIcon}
            color={"white"}
            width={25}
            height={20}
          />
        }
        type={"text"}
      />
      <IconFormInput
        editMode={true}
        className="mb-0"
        name={"password"}
        rules={[{ required: true, message: t("translation:login.form.password.enter") }]}
        placeholder={t("translation:login.form.password.placeholder")}
        icon={
          <SvgConverter
            Icon={LockIcon}
            color={"white"}
            width={25}
            height={25}
          />
        }
        type={"password"}
      />
      <div className="text-right">
        <Link
          id={uniqueID()}
          to={AUTH_ROUTES.FORGOT_PASSWORD}
          type="link"
          size={"small"}
          className="forgot-password-text"
        >
          {t("translation:login.form.link")}
        </Link>
      </div>
      <AButton
        type="primary"
        id={uniqueID()}
        loading={user.progressing}
        htmlType="submit"
        className="background-header mt-30 w-100 br-4"
      >
        <span className="auth-button-labels mt-n2">{t("translation:login.form.button")}</span>
      </AButton>
    </Form>
  );
};

export default LoginForm;
